<template>
    <el-select
            v-model="inner_value"
            style="width: 100%"
            filterable
            :clearable="clearable"
            :multiple="multiple"
            :placeholder="placeholder"
            :loading="query_loading"
            :size="size"
            :disabled="disabled"
    >
        <slot></slot>
        <el-option
                v-for="(item, index) in options"
                :label="option_formatter?option_formatter(item, index):(Array.isArray(display_field)?display_field.map((key)=>item[key]).join(' | '):item[display_field])"
                :disabled="disabled_field?item[disabled_field]:false"
                :key="value_key==undefined?index:item[value_field]"
                :value="value_field==undefined?item:item[value_field]"
        >
        </el-option>
    </el-select>
</template>

<script>
    import axios from "axios"
    export default {
        name: "GRemoteSelect",
        props: {
            value: {
            },
            params: {
                type: Object,
                default: () => {return {}}
            },
            placeholder: {
                default: "输入关键字搜索"
            },
            multiple: {
                type: Boolean,
                default: false
            },
            clearable: {
                type: Boolean,
                default: false
            },
            url: {
                type: String,
                required: true
            },
            results_field: {
                default: "results"
            },
            value_field: {
                type: String,
                required: false
            },
            display_field: {
                required: true
            },
            disabled_field: {
                type: String,
                required: false
            },
            size: {
                type: String,
                default: "medium"
            },
            value_key: {
                type: String,
                default: "value"
            },
            disabled: Boolean,
            disable_query: Boolean,
            option_formatter: Function
        },
        data() {
            return {
                inner_value: this.value,
                options: [],
                filtered_options: [],
                query_loading: false
            };
        },
        mounted() {
            if (this.value_field == undefined && this.value_key == undefined) {
                throw Error("prop 参数 value_field 和 key_field 不能同时为空 ");
            }
            this.get_options();
        },
        methods: {
            get_options() {
                if(this.disable_query){
                    return
                }
                this.g_master_api
                    .get(this.url, { params: this.params })
                    .then(response => {
                        if (this.results_field) {
                            this.options = response.data[this.results_field];
                        } else {
                            this.options = response.data;
                        }
                    })
                    .catch(error => {
                        if (axios.isCancel(error)) {
                            console.log("remote select cancel error")
                            return
                        }
                        this.$message.error("获取下拉列表失败");
                    });
            },
            clean_options(){
                this.options = []
            }
            // option_query(query) {
            //   if (!query) {
            //     this.filtered_options = this.options.slice(0, 10);
            //     return;
            //   }
            //   const lower_case_query = query.toLowerCase();
            //   this.query_loading = true;
            //   var filtered_options = [];
            //   for (let item of this.options) {
            //     if (filtered_options.length >= 10) {
            //       break;
            //     }
            //     if (
            //       String(item[this.value_field])
            //         .toLowerCase()
            //         .indexOf(lower_case_query) > -1 ||
            //       String(item[this.display_field])
            //         .toLowerCase()
            //         .indexOf(lower_case_query) > -1
            //     ) {
            //       filtered_options.push(item);
            //     }
            //   }
            //   this.filtered_options = filtered_options;
            //   this.query_loading = false;
            // }
        },
        watch: {
            value(new_value, old_value) {
                if (new_value === old_value) {
                    return;
                }
                this.inner_value = new_value;
            },
            inner_value(new_value) {
                this.$emit("input", new_value);
                this.$emit("change", new_value);
            },
            url(new_value, old_value) {
                this.get_options();
            },
            params(new_value, old_value) {
                if(JSON.stringify(new_value) == JSON.stringify(old_value)){
                    return
                }
                this.get_options();
            }
        }
    };
</script>
