<script>
    import df_admin from "df-admin";

    export default {
        mixins: [df_admin.mixins.list_mixin],
        data() {
            return {
                url: "release/instances",
                params: {
                    app__name: this.$store.state.current_app_name,
                    pool__key: null
                },
                selected_list: [],
                // start_loading: false,
                // stop_loading: false,
                loading_list: {
                    start: false,
                    stop: false,
                    restart: false
                },
                interval_id: null,
                run_status_loading: false,
                last_pool_key_store_key: "instance__pool_key"
            };
        },
        mounted() {
            var pool_key = localStorage.getItem(this.last_pool_key_store_key)
            if (pool_key) {
                this.params.pool__key = pool_key
                this.get_data()
            }
            this.interval_id = setInterval(this.after_get_data, 5000);
        },
        methods: {
            after_get_data() {
                if (this.data.length > 0) {
                    this.get_status_all()
                }
            },
            get_status_all() {
                var data = {
                    instance_list: this.data,
                    pool_key: this.params.pool__key
                }
                this.run_status_loading = true
                this.$c_master.post("release/instances/status-batch", data).then(response => {
                    for (let i = 0; i < this.data.length; i++) {
                        this.run_status_loading = false
                        this.$set(this.data[i], "run_status", response.data.results[i])
                        // this.data[i].run_status = response.data.results[i]
                        // console.log("this.data[i]: ", this.data[i])
                    }
                }).catch(error => {
                    this.run_status_loading = false
                    console.error(error)
                    this.$message.error("获取运行状态失败")
                })
            },
            handleSelectionChange(val) {
                this.selected_list = val;
            },
            stop_list(instance_list) {
                for (let instance of instance_list) {
                    this.$set(instance, "_loading", true)
                }
                this.stop_loading = true
                var data = {
                    instance_list: instance_list,
                    pool_key: this.params.pool__key
                }
                this.$c_master.post("release/stop-list", data).then(response => {
                    for (let instance of instance_list) {
                        this.$set(instance, "_loading", false)
                    }
                    this.stop_loading = false
                    if (response.data.succeeded_count) {
                        this.$message.success(`${response.data.succeeded_count} succeeded`)
                    }
                    if (response.data.failed_count) {
                        this.$message.warning(`${response.data.failed_count} failed`)
                    }
                }).catch(error => {
                    for (let instance of instance_list) {
                        this.$set(instance, "_loading", false)
                    }
                    this.stop_loading = false
                })
            },
            batch_action(action_name, instance_list) {
                this.$confirm(`${action_name}?`, "Warning", {
                    confirmButtonText: "Confirm",
                    cancelButtonText: "Cancel",
                    type: "warning",
                    beforeClose: (action, instance, done) => {
                        if (action === 'confirm') {
                            instance.confirmButtonLoading = true;

                            for (let instance of instance_list) {
                                this.$set(instance, `${action_name}_loading`, true)
                            }
                            this.loading_list[action_name] = true
                            var data = {
                                instance_list: instance_list,
                                pool_key: this.params.pool__key
                            }
                            this.$c_master.post(`release/instances/${action_name}-batch`, data).then(response => {
                                instance.confirmButtonLoading = false;
                                done()
                                for (let instance of instance_list) {
                                    this.$set(instance, `${action_name}_loading`, false)
                                }
                                this.loading_list[action_name] = false
                                if (response.data.succeeded_count) {
                                    this.$message.success(`${response.data.succeeded_count} succeeded`)
                                }
                                if (response.data.failed_count) {
                                    this.$message.warning(`${response.data.failed_count} failed`)
                                }
                            }).catch(error => {
                                instance.confirmButtonLoading = false;
                                for (let instance of instance_list) {
                                    this.$set(instance, `${action_name}_loading`, false)
                                }
                                this.loading_list[action_name] = false
                            })
                        } else {
                            done()
                        }
                    }
                })

            },
            on_start_batch(instance_list) {
                this.batch_action("start", instance_list)
            },
            on_restart_batch(instance_list) {
                this.batch_action("restart", instance_list)
            },
            on_stop_batch(instance_list) {
                this.batch_action("stop", instance_list)
            },
            // start_list(instance_list) {
            //     for (let instance of instance_list) {
            //         this.$set(instance, "_loading", true)
            //     }
            //     this.start_loading = true
            //     var data = {
            //         instance_list: instance_list,
            //         pool_key: this.params.pool__key
            //     }
            //     this.$c_master.post("release/start-list", data).then(response => {
            //         for (let instance of instance_list) {
            //             this.$set(instance, "_loading", false)
            //         }
            //         this.start_loading = false
            //         if (response.data.succeeded_count) {
            //             this.$message.success(`${response.data.succeeded_count} succeeded`)
            //         }
            //         if (response.data.failed_count) {
            //             this.$message.warning(`${response.data.failed_count} failed`)
            //         }
            //     }).catch(error => {
            //         for (let instance of instance_list) {
            //             this.$set(instance, "_loading", false)
            //         }
            //         this.start_loading = false
            //     })
            // },
            on_click_history(instance) {
                this.add_dialog('LInstanceHistory', {instance: instance})
            },
            on_change_pool(pool_key) {
                localStorage.setItem(this.last_pool_key_store_key, pool_key)
                this.get_data()
            }
        },
        beforeDestroy() {
            if (this.interval_id) {
                clearInterval(this.interval_id);
            }
        }
    };
</script>
