        <el-input
                placeholder="Filter keyword"
                v-model="filterText">
        </el-input>
        <el-tree
                v-loading="loading"
                :data="tree_data"
                show-checkbox
                node-key="id"
                :filter-node-method="filterNode"
                ref="tree"
                :default-expanded-keys="['_all']"
                highlight-current
                @check="on_check">
