</template>

<script>
    export default {
        name: "GEnvTree",
        // props: ["value"],
        props: {
            "value": {
                default: () => []
            }
        },
        data() {
            return {
                filterText: '',
                loading: false,
                tree_data: [],
                // inner_value: [],
                // props: {
                //     children: 'children',
                //     label: 'label',
                //     disabled: 'disabled',
                // }
            }
        },
        mounted() {
            this.get_tree_data()
            this.$refs.tree.setCheckedKeys(this.value);
        },
        methods: {
            get_tree_data() {
                // var url = `release/apps/${$store.state.current_app_id}/tree`
                var url = `release/apps/${this.$store.state.current_app_id}/tree`
                this.loading = true
                this.$c_master.get(url).then(response => {
                    this.loading = false
                    this.tree_data = response.data.results
                }).then(error => {
                    this.loading = false
                })
            },
            on_check(args1, checked) {
                var checked_keys = checked.checkedKeys.filter(item => item != undefined)
                this.$emit("input", checked_keys)
                this.$emit("change", checked_keys)
            },
            filterNode(value, data) {
                if (!value) return true;
                return data.label.indexOf(value) !== -1;
            },
            console_log(data) {
                console.log(data)
            }
        },
        watch: {
            // value(new_value, old_value) {
