<template>
    <el-drawer
            title="我是标题"
            :visible.sync="is_visible"
            :before-close="on_before_close"
            ref="aaaa"
            size="1000px"
    >
        <g-flow-chart
                style="height: 400px"
                @on_click_node="on_click_node"
                @external_objects_dropped="create_node"
                @selection_moved="move_node"
                @selection_deleted="delete_node"
                ref="flow_chart"
        ></g-flow-chart>
        <el-button @click="save_links">保存连接</el-button>
    </el-drawer>
</template>

<script>
    import axios from "axios"
    import dialog_mixin from "../../../utils/mixins/dialog_mixin";

    export default {
        props: ["template"],
        mixins: [dialog_mixin],
        data() {
            return {
                type_label_value_map: null,
                node_id_node_map: {},
                type_value_label_map: {},
                item: {
                    id: 1
                },
                // id_category_map: {
                //     1: "Start",
                //     1: "End",
                //     1: "Start",
                //     1: "Start",
                //     1: "Start",
                // }
            }
        },
        mounted() {
            this.get_type_map()
            this.load_data()
        },
        methods: {
            on_click_node() {

            },
            create_node(node_data) {
                var location_x = Number(node_data.loc.split(" ")[0])
                var location_y = Number(node_data.loc.split(" ")[0])
                var data = {
                    "template": this.template.id,
                    "name": node_data.text,
                    "type": this.type_label_value_map[node_data.category],
                    "location_x": location_x,
                    "location_y": location_y
                }
                this.g_master_api.post("workflow/nodes", data = data).then(response => {
                    this.$refs.flow_chart.set_key_for_node_data(node_data, response.data.id)
                    this.node_id_node_map[response.data.id] = response.data
                })
            },
            get_type_map() {
                var self = this
                this.g_master_api.get("workflow/nodes/get-choices?field_name=type").then(response => {
                    this.type_label_value_map = response.data["label_value_map"]
                    this.type_value_label_map = response.data["value_label_map"]
                })
            },
            delete_node(node_data) {
                var url = `workflow/nodes/${node_data.key}`
                this.g_master_api.delete(url).then(response => {
                    this.$message.success("删除成功")
                })
            },
            move_node(node_data) {
                var location_x = Number(node_data.loc.split(" ")[0])
                var location_y = Number(node_data.loc.split(" ")[0])
                var url = `workflow/nodes/${node_data.key}`
                var data = {
                    location_x: location_x,
                    location_y: location_y
                }
                this.g_master_api.patch(url, data = data)
            },
            save_links() {
                var data = this.$refs.flow_chart.get_data();
                console.log("data: ", data)
                console.log("data.linkDataArray: ", data.linkDataArray)
                var link_list = []
                for (let item of data.linkDataArray) {
                    var link = {
                        "template": this.template.id,
                        "from_node": item.from,
                        "to_node": item.to,
                    }
                    link_list.push(link)
                }
                var data = {"link_list": link_list}
                var url = `workflow/templates/${this.template.id}/set-link`
                this.g_master_api.post(url, data).then(response => {
                    this.$message.success("保存成功")
                })
            },
            load_data() {
                var node_params = {
                    template_id: this.template.id,
                    page_size: 100
                };
                var link_params = {
                    template_id: this.template.id,
                    page_size: 100
                };
                var self = this
                var node_promise = this.g_master_api.get("workflow/nodes", {params: node_params})
                var link_promise = this.g_master_api.get("workflow/links", {params: link_params})
                axios.all([node_promise, link_promise]).then(axios.spread(function (node_response, link_response) {
                    console.log("node_response: ", node_response)
                    var node_data_list = []
                    for (let node of node_response.data.results) {
                        var item = {
                            "category": self.type_value_label_map[node.type],
                            "key": node.id,
                            "loc": `${node.location_x} ${node.location_y}`,
                            "text": node.name
                        }
                        node_data_list.push(item)
                    }
                    var link_data_list = []
                    for (let link of link_response.data.results) {
                        var item = {
                            "from": link.from_node,
                            "to": link.to_node,
                            "from_port": link.from_port,
                            "to_port": link.to_port
                        }
                        link_data_list.push(item)
                    }
                    var data = {
                        "class": "go.GraphLinksModel",
                        "linkfrom_portIdProperty": "from_port",
                        "linkto_portIdProperty": "to_port",
                        "nodeDataArray": node_data_list,
                        "linkDataArray": link_data_list,
                    }
                    console.log("data: ", data)
                    self.$refs.flow_chart.set_data(data)
                    // var nodes_data = []
                    // for (item of nodes) {
                    //     // item_data = {"from": from_node, "to": to_node, ""}
                    // }
                }))
            }
        }
    }
</script>