<template>
    <el-drawer
            :title="template?`修改模板-${template.name}`:'添加模板'"
            width="1200px"
            :visible.sync="is_visible"
            @before-close="on_before_close"
    >
        <div v-loading="loading">
            <el-tabs v-model="active">
                <el-tab-pane
                        label="基本"
                        name="0"
                >
                    <el-form
                            label-width="100px"
                            style="height: 330px; margin-right: 50px; padding-top: 30px"
                    >
                        <el-row>
                            <el-col :span="12">
                                <el-form-item
                                        label="节点名:"
                                        :error="form_errors.name"
                                        required
                                >
                                    <el-input v-model="form_data.name"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                            </el-col>
                        </el-row>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane
                        label="表 单"
                        name="1"
                >
                    <el-form
                            label-width="100px"
                            style="height: 360px; margin-right: 50px"
                    >
                        <el-form-item
                                label="表单schema"
                                :error="form_errors.application_schema"
                        >
                            <base-monaco-editor
                                    style="height: 350px"
                                    v-model="form_data.application_schema"
                                    language="json"
                            ></base-monaco-editor>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane
                        label="验证"
                        name="2"
                >
                    <el-form
                            label-width="100px"
                            style="height: 360px; margin-right: 50px"
                    >
                        <el-form-item
                                label="申请验证脚本"
                                :error="form_errors.verification_script"
                        >
                            <base-monaco-editor
                                    style="height: 350px"
                                    v-model="form_data.verification_script"
                                    language="python"
                            ></base-monaco-editor>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane
                        label="处理脚本"
                        name="6"
                >
                    <el-form
                            label-width="100px"
                            style="height: 360px; margin-right: 50px"
                    >
                        <el-form-item
                                label="脚本"
                                :error="form_errors.script"
                        >
                            <base-monaco-editor
                                    style="height: 330px"
                                    v-model="form_data.script"
                                    language="python"
                            ></base-monaco-editor>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
            </el-tabs>
            <el-button-group>
                <el-button
                        type="primary"
                        icon="el-icon-arrow-left"
                        @click="on_previous_step"
                        :disabled="Number(active)<=0"
                >上一步</el-button>
                <el-button
                        type="primary"
                        @click="on_next_step"
                        :disabled="Number(active)>=6"
                >下一步
                    <i class="el-icon-arrow-right el-icon--right"></i>
                </el-button>
            </el-button-group>
            <el-button
                    style="float: right"
                    type="primary"
                    @click="on_submit"
                    :icon="template?'el-icon-refresh':'el-icon-plus'"
                    :loading="submit_loading"
                    element-loading-spinner="el-icon-loading"
            >{{template?"更新":"创建"}}</el-button>
        </div>
    </el-drawer>
</template>
<script>
    import store from "../../../store";
    import variables from "../../../utils/variables";
    export default {
        props: {
            template: {
                default: null,
                required: false
            }
        },
        data() {
            return {
                active: "0",
                is_visible: true,
                store: store,
                form_data: this.template
                    ? {}
                    : { disabled: false, approval_node_start: [], generation: 1 },
                form_errors: {},
                groups: [],
                loading: false,
                submit_loading: false,
                schema_placeholder: variables.schema_placeholder,
                variables: variables,
                appl_options: {
                    mode: "text/javascript"
                }
            };
        },
        mounted() {
            if (this.template) {
                // if (
                //   !store.state.user_info.is_staff &&
                //   this.template.creator != store.state.user_info.id
                // ) {
                //   this.$message.info("非创建人无法修改模板 且只能查看部分参数");
                // }
                this.get_data();
            }
            // if (!store.state.user_info.is_staff) {
            //   this.$message.warning("抱歉 您没有权限创建模板 请联系管理员");
            // }
        },
        methods: {
            get_data() {
                const url = `work/template/${this.template.id}`;
                this.loading = true;
                this.g_master_api
                    .get(url)
                    .then(response => {
                        this.loading = false;
                        this.init_data(response.data);
                        // response.data.application_schema = JSON.stringify(
                        //   response.data.application_schema,
                        //   null,
                        //   "\t"
                        // );
                        // // response.data.approval_schema = JSON.stringify(
                        // //   response.data.approval_schema
                        // // );
                        // if (!response.data.approval_node_start) {
                        //   response.data.approval_node_start = [];
                        // }
                        // this.form_data = response.data;
                    })
                    .catch(error => {
                        this.loading = false;
                    });
            },
            init_data(data) {
                data.application_schema = JSON.stringify(
                    data.application_schema,
                    null,
                    "\t"
                );
                data.script_schema = JSON.stringify(data.script_schema, null, "\t");
                if (!data.approval_node_start) {
                    data.approval_node_start = [];
                }
                this.form_data = data;
            },
            on_submit() {
                const url = this.template
                    ? `work/template/${this.template.id}`
                    : "work/template";
                var form_data = Object.assign({}, this.form_data);
                if (!form_data.application_schema) {
                    form_data.application_schema = "{}";
                }
                // if (!form_data.approval_schema) {
                //   form_data.approval_schema = "{}";
                // }
                if (!form_data.script_schema) {
                    form_data.script_schema = "{}";
                }
                try {
                    form_data.application_schema = JSON.parse(form_data.application_schema);
                } catch (error) {
                    this.$message.error("申请参数schema必须为json格式 请检查格式是否有误");
                    return;
                }
                try {
                    form_data.script_schema = JSON.parse(form_data.script_schema);
                } catch (error) {
                    this.$message.error("脚本参数schema必须为json格式 请检查格式是否有误");
                    return;
                }
                // for (let item of form_data.approval_node_start) {
                //   item.approval_schema = JSON.parse(item.approval_schema);
                // }
                // try {
                //   form_data.approval_schema = JSON.parse(form_data.approval_schema);
                // } catch (error) {
                //   this.$message.error("审批参数必须为json格式 请检查格式是否有误");
                //   return;
                // }
                this.submit_loading = true;
                this.g_master_api({
                    method: this.template ? "put" : "post",
                    url,
                    data: form_data
                })
                    .then(response => {
                        this.submit_loading = false;
                        this.$message.success(this.template ? "更新成功" : "创建成功");
                        this.add_update_template(response.data);
                        this.on_close();
                    })
                    .catch(error => {
                        this.submit_loading = false;
                        if (error.response) {
                            this.form_errors = error.response.data;
                        }
                    });
            },
            add_update_template(new_template) {
                // if (this.template) {
                //   for (let key in this.template) {
                //     this.template[key] = new_template[key];
                //   }
                //   return;
                // }
                // for (let group of this.store.state.temp_groups) {
                //   if (new_template.group == group.id) {
                //     group.templates.push(new_template);
                //     return;
                //   }
                // }
                this.$emit("refresh");
            },
            create() {},
            update() {},
            on_previous_step() {
                this.active = String(Number(this.active) - 1);
            },
            on_next_step() {
                this.active = String(Number(this.active) + 1);
            },
            on_close(confirm = false) {
                if (confirm) {
                    this.is_visible = true;
                    this.$confirm("确认退出编辑", "警告", {
                        confirmButtonText: "确定",
                        cancelButtonText: "取消",
                        type: "warning"
                    })
                        .then(() => {
                            this.$el.parentNode.removeChild(this.$el);
                            this.$destroy();
                        })
                        .catch(() => {});
                } else {
                    this.$el.parentNode.removeChild(this.$el);
                    this.$destroy();
                }
            }
        }
    };
</script>