<template>
    <el-drawer
            title="Log"
            :visible.sync="is_visible"
            :before-close="on_before_close"
            append-to-body
            size="50%"
    >
        <el-tabs>
            <el-tab-pane v-model="current_activate" @handleClick="console.log(current_activate)"
                         v-for="(item, index) in data"
                         :key="String(item.id)" :label="`第${data.length - index}次`"
                         :name="String(index)">
                <table
                        border="0"
                        style="width: 650px; table-layout:fixed; word-wrap:break-word; word-break:break-all;"
                >
                    <tr>
                        <td class="label">start_time:</td>
                        <td>{{ item.create_time | df_datetime }}</td>
                        <td class="label">end_time:</td>
                        <td>{{ item.update_time | df_datetime }}</td>
                    </tr>
                    <tr>
                        <td
                                class="label"
                                colspan="1"
                        >content:
                        </td>
                        <td colspan="3">
                            <pre style="max-height: 300px; overflow: auto">{{ item.content }}</pre>
                        </td>
                    </tr>
                </table>
            </el-tab-pane>
        </el-tabs>
        <div style="position: absolute; right: 30px; bottom: 30px">
            <el-button
                    size="medium"
                    type="primary"
                    :loading="retry_continue_loading"
                    style="margin: 20px; float: right"
                    @click="on_retry_continue"
            >Retry & continue
            </el-button>
            <el-button
                    size="medium"
                    type="primary"
                    :loading="skip_continue_loading"
                    style="margin: 20px; float: right"
                    @click="on_skip_continue"
            >Skip & continue
            </el-button>
        </div>
    </el-drawer>
</template>

<script>

    export default {
        props: ["node"],
        data() {
            return {
                current_activate: "",
                skip_loading: false,
                is_visible: true,
                url: "release/logs",
                params: {
                    record: this.node.record,
                    stage: this.node.stage,
                    ordering: "-id"
                },
                data: [],
                close_confirm: false,
                submit_loading: false,
                retry_continue_loading: false,
                skip_continue_loading: false,
                interval_id: null
            }
        },
        mounted() {
            this.get_data()
            this.interval_id = setInterval(this.get_data, 5000);
        },
        methods: {
            on_retry_continue() {
                var data = {
                    stage: this.node.stage
                }
                var url = `release/instance-release-records/${this.node.record}/retry-continue`
                this.retry_continue_loading = true
                this.$c_master.post(url, data).then(response => {
                    this.retry_continue_loading = false
                    this.$message.success("Started")
                }).catch(error => {
                    this.retry_continue_loading = false
                })
            },
            on_skip_continue() {
                var data = {
                    stage: this.node.stage
                }
                var url = `release/instance-release-records/${this.node.record}/skip-continue`
                this.skip_continue_loading = true
                this.$c_master.post(url, data).then(response => {
                    this.skip_continue_loading = false
                    this.$message.success("Skipped")
                }).catch(error => {
                    this.skip_continue_loading = false
                })
            },
            get_data() {
                this.$c_master.get(this.url, {params: this.params}).then(response => {
                    this.data = response.data.results
                    if (this.data.length > 0) {
                        this.current_activate = "0"
                    }
                })
            },
            on_before_close(done) {
                if (this.close_confirm) {
                    this.$confirm('确认关闭？')
                        .then(_ => {
                            done();
                            setTimeout(this._remote_child, 1000)
                            // this._remote_child()
                        })
                        .catch(_ => {
                        });
                } else {
                    done();
                    setTimeout(this._remote_child, 1000)
                    // this._remote_child()
                }
            },
            _remote_child() {
                this.before_remote()
                if (this.$el.parentNode) {
                    this.$el.parentNode.removeChild(this.$el);
                }
            },
            before_remote() {
                if (this.interval_id) {
                    clearInterval(this.interval_id);
                }
            }
        }
    }
</script>


<style scoped>
    .label {
        color: #99a9bf;
        text-align: right;
        padding-right: 5px;
        width: 120px;
    }
</style>
