<template>
    <el-dialog
            title="graph"
            width="800px"
            :visible.sync="is_visible"
            @close="on_close"
    >
        <div v-loading="loading">
            <!--        <df-date-range type="datetimerange" :value="params" start_field="start_time" end_field="end_time"></df-date-range>-->
            <ve-line :data="chart_data"c></ve-line>
        </div>
    </el-dialog>
</template>

<script>
    import df_admin from "df-admin";

    export default {
        mixins: [df_admin.mixins.dialog_mixin],
        props: ["storage"],
        data() {
            return {
                // chart_settings: {
                //     yAxisType: ['KMB'],
                // },
                chart_data: {
                    columns: ['datetime', 'real', "pred"],
                    rows: []
                },
                params: {
                    history_days: 10,
                    predictions_days: 30
                },
                loading: false
            }
        },
        mounted() {
            this.get_data()
        },
        methods: {
            get_data() {
                this.loading = true
                const url = `storage/storages/${this.storage.id}/graph`
                this.$c_master.get(url, {params: this.params}).then(response => {
                    this.chart_data.rows = response.data.results
                    this.loading = false
                    console.log(this.chart_data)
                }).catch(error => {
                    this.loading = false
                })
            }
        }
    };
</script>
