<template>
    <el-drawer
            title="Pipeline"
            width="800px"
            :visible.sync="is_visible"
            :before-close="on_before_close"
            size="80%"
    >
        <div v-loading="loading">
            <vue-pipeline :data="data" @select="on_select" :xstep="180"></vue-pipeline>
        </div>
<!--        <el-button @click="on_click">sss</el-button>-->
        <el-button type="primary" style="position: absolute; right: 30px; bottom: 30px" :loading="on_retry_all_loading"
                   @click="on_retry_all">Retry all
        </el-button>
    </el-drawer>
</template>

<script>
    import df_admin from "df-admin";

    export default {
        // mixins: [df_admin.mixins.dialog_mixin],
        mixins: [df_admin.mixins.drawer],
        props: ["release"],
        data() {
            return {
                view_first: true,
                data: [{
                    "name": "Start",
                    "status": "start",
                    "next": []
                }],
                loading: false,
                close_confirm: false,
                on_retry_all_loading: false,
                interval_id: null
            }
        },
        mounted() {
            this.get_data()
            this.interval_id = setInterval(this.get_data, 5000);
        },
        methods: {
            on_click() {
                this.data = [{
                    "name": "End",
                    "status": "end",
                    "next": []
                }]
            },
            get_data() {
                this.loading = true
                const url = `release/releases/${this.release.id}/pipeline`
                this.$c_master.get(url).then(response => {
                    this.data = response.data.results
                    this.view_first = !this.view_first
                    this.loading = false
                }).catch(error => {
                    this.loading = false
                })
            },
            on_select(node) {
                console.log(this)
                this.$df_add_dialog('LReleaseNodeDetail', {'node': node})
            },
            on_retry_all() {
                this.on_retry_all_loading = true
                const url = `release/releases/${this.release.id}/retry-all`
                this.$c_master.post(url).then(response => {
                    this.on_retry_all_loading = false
                    this.$message.success(`Start to retry the ${response.data.count} failed instances`)
                }).catch(error => {
                    this.on_retry_all_loading = false
                })
            },
            on_before_close(done) {
                if (this.close_confirm) {
                    this.$confirm('确认关闭？')
                        .then(_ => {
                            done();
                            setTimeout(this._remote_child, 1000)
                            // this._remote_child()
                        })
                        .catch(_ => {
                        });
                } else {
                    done();
                    setTimeout(this._remote_child, 1000)
                    // this._remote_child()
                }
            },
            _remote_child() {
                this.before_remote()
                if (this.$el.parentNode) {
                    this.$el.parentNode.removeChild(this.$el);
                }
            },
            before_remote() {
                if (this.interval_id) {
                    clearInterval(this.interval_id);
                }
            }

        }
    };
</script>
