<template>
    <el-dropdown :size="size" @command="on_command">
        <span class="el-dropdown-link" style="color: #FFFFFF" :style="{fontSize: font_size}">
            {{ inner_value==undefined?this.placeholder:id_name_map[inner_value] }}<i
                class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="item in options" :index="item[value_field]" :command="item[value_field]">{{
                item[display_field] }}
            </el-dropdown-item>
            <!--            <el-dropdown-item command="a">黄金糕</el-dropdown-item>-->
            <!--            <el-dropdown-item command="b">狮子头</el-dropdown-item>-->
            <!--            <el-dropdown-item command="c">螺蛳粉</el-dropdown-item>-->
            <!--            <el-dropdown-item command="d" disabled>双皮奶</el-dropdown-item>-->
            <!--            <el-dropdown-item command="e" divided>蚵仔煎</el-dropdown-item>-->
        </el-dropdown-menu>
    </el-dropdown>
</template>

<script>
    import axios from "axios"

    export default {
        name: "GRemoteDropdown",
        props: {
            value: {},
            params: {
                type: Object,
                default: () => {
                    return {}
                }
            },
            placeholder: {
                default: "输入关键字搜索"
            },
            multiple: {
                type: Boolean,
                default: false
            },
            clearable: {
                type: Boolean,
                default: false
            },
            url: {
                type: String,
                required: true
            },
            results_field: {
                default: "results"
            },
            value_field: {
                type: String,
                required: true
            },
            display_field: {
                required: true
            },
            disabled_field: {
                type: String,
                required: false
            },
            size: {
                type: String,
                default: ""
            },
            font_size: {
                type: String,
                default: "20px"
            },
            value_key: {
                type: String,
                default: "value"
            },
            disabled: Boolean,
            disable_query: Boolean,
            option_formatter: Function,
            auto_select_first: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                inner_value: this.value,
                options: [],
                filtered_options: [],
                query_loading: false,
                id_name_map: {},
                id_node_map: {}
            };
        },
        mounted() {
            if (this.value_field == undefined && this.value_key == undefined) {
                throw Error("prop 参数 value_field 和 key_field 不能同时为空 ");
            }
            this.get_options();
        },
        methods: {
            on_command(command) {
                var name = this.id_name_map[command]
                var node = this.id_node_map[command]
                this.inner_value = command
                this.$emit("input", command);
                this.$emit("change", command, name, node);
            },
            get_options() {
                if (this.disable_query) {
                    return
                }
                this.$c_master
                    .get(this.url, {params: this.params})
                    .then(response => {
                        if (this.results_field) {
                            this.options = response.data[this.results_field];
                        } else {
                            this.options = response.data;
                        }
                        // this.options.forEach(item => {
                        //     this.map[item[this.value_field]] = item[this.display_field]
                        // })
                        for (let item of this.options) {
                            // console.log("item: ", item)
                            this.id_name_map[item[this.value_field]] = item[this.display_field]
                            this.id_node_map[item[this.value_field]] = item
                            // console.log("this.id_name_map: ", this.id_name_map)
                        }
                        if (this.auto_select_first && this.options.length) {
                            this.on_command(this.options[0][this.value_field])
                        }
                    })
                    .catch(error => {
                        console.error(error)
                        if (axios.isCancel(error)) {
                            console.log("remote select cancel error")
                            return
                        }
                        this.$message.error("获取下拉列表失败");
                    });
            },
            clean_options() {
                this.options = []
            }
        },
        watch: {
            value(new_value, old_value) {
                if (new_value === old_value) {
                    return;
                }
                this.inner_value = new_value;
            },
            // inner_value(new_value) {
            //     this.$emit("input", new_value);
            //     this.$emit("change", new_value);
            // },
            url(new_value, old_value) {
                this.get_options();
            },
            params(new_value, old_value) {
                if (JSON.stringify(new_value) == JSON.stringify(old_value)) {
                    return
                }
                this.get_options();
            }
        }
    };
</script>
