<template>
    <div
            style="height: 100%"
            @keyup.ctrl.83="emit('save', this.editor.getValue())"
    ></div>
</template>


<script>
    import { editor } from "monaco-editor";
    import { type } from "os";
    export default {
        name: "GMonacoEditor",
        props: {
            value: {
                type: String,
                default: ""
            },
            language: {
                type: String,
                default: "other"
            },
            theme: {
                type: String,
                default: "vs-dark"
            },
            options: {
                type: Object,
                default: () => {
                    return {};
                }
            }
        },
        data() {
            return {
                console: console,
                editor: null,
                aaaaaa: "lalalalalal"
            };
        },
        mounted() {
            this.init_monaco();
        },
        methods: {
            init_monaco() {
                const options = Object.assign(
                    {
                        value: this.value,
                        theme: this.theme,
                        language: this.language,
                        automaticLayout: true
                    },
                    this.options
                );
                this.editor = editor.create(this.$el, options);
                this.editor.onDidChangeModelContent(event => {
                    const value = this.editor.getValue();
                    if (this.value !== value) {
                        this.$emit("input", value);
                    }
                });
            }
        },
        watch: {
            options: {
                deep: true,
                handler(options) {
                    if (this.editor) {
                        this.editor.updateOptions(options);
                    }
                }
            },
            value(newValue) {
                if (this.editor) {
                    if (newValue !== this.editor.getValue()) {
                        this.editor.setValue(newValue);
                    }
                }
            },
            language(newVal) {
                if (this.editor) {
                    window.monaco.editor.setModelLanguage(this.editor.getModel(), newVal);
                }
            },
            theme(newVal) {
                if (this.editor) {
                    window.monaco.editor.setTheme(newVal);
                }
            }
        }
    };
</script>
